.links {
    position: sticky;
    z-index: 3;
    top: 0;
    display: flex;
    justify-content: space-between;
    background-color: rgb(255, 255, 255);
    padding: 10px 0px;
  }
  nav button {
    width: 250px;
    font-size: 20px;
    background-color: tomato;
  }
  
  .header-dropdown {
    margin-top: 0;
  }
  
  .shopping-overlay {
    width: 300px;
    display: flex;
    justify-content: flex-end;
    padding-right: 120px;
  }
  .shopping-bag img {
    position: relative;
    width: 30px;
    z-index: 100;
  }
  .shopping-bag :hover {
    cursor: pointer;
  }
  
  .shopping-bag span {
    position: relative;
    z-index: 1000;
    bottom: 15px;
    right: 15px;
    top: 1px;
    color: white;
    font-weight: 800;
    font-size: 10px;
    background-color: #cb0e4e;
    border-radius: 50%;
    width: 36px;
    height: 30px;
    padding: 3px 6px;
  }
  
  .dropdown-list {
    position: absolute;
    opacity: 0;
  }
  .dropdown-list.visible {
    position: absolute;
    background-color: white;
    opacity: 1;
    z-index: 100;
    width: 320px;
    display: flex;
    margin-right: 70px;
    margin-top: 30px;
    flex-direction: column;
    align-items: center;
    padding: 30px 10px;
    border-radius: 5px;
    -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.37);
    box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.37);
    max-height: 80vh;
    overflow-y: scroll;
  }
  
  .dropdown-overlay {
    position: fixed;
    right: 20px;
    top: 20px;
    width: 350px;
  }
  .dropdown-overlay.visible {
    height: 200px;
  }
  
  .dropdown-list.visible::before {
    content: " ";
    position: absolute;
    right: 50px;
    top: -15px;
    border-top: none;
    border-right: 15px solid transparent;
    border-left: 15px solid transparent;
    border-bottom: 15px solid rgb(255, 255, 255);
  }
  
  .total p {
    width: 300px;
    padding: 15px 0px;
    border-top: 1px solid rgb(217, 217, 217);
    border-bottom: 1px solid rgb(217, 217, 217);
  
    text-align: center;
  }
  .total span {
    color: gray;
  }
  
  .dropdown-product {
    display: grid;
    grid-template-columns: 100px 200px;
  }
  
  .dropdown-image {
    width: 100px;
  }
  .dropdown-text {
    grid-column: 2/3;
  }
  .dropdown-text p:hover {
    text-decoration: underline;
    cursor: pointer;
  }
  
  .dropdown-numbers {
    display: flex;
    gap: 15px;
  }
  .dropdown-quantity {
    color: rgb(173, 173, 173);
  }
  