.shop {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .shop h1 {
    border-bottom: 2px solid gray;
    padding: 20px 40vw;
  }
  
  .products {
    display: grid;
    grid-template-columns: 250px 250px 250px 250px;
    gap: 30px;
    justify-content: center;
  }
  
  .product-card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 400px;
  }
  .product-card:hover {
    cursor: pointer;
  }
  
  .product-card img {
    width: 236px;
  }
  .product-card p {
    margin: 5px 0;
  }
  .card-header {
    font-size: 14px;
    font-weight: 500;
    color: rgb(112, 112, 112);
  }
  .card-author {
    font-weight: 400;
    font-size: 10;
    color: rgb(178, 178, 178);
  }
  .card-price {
    font-size: 16px;
    color: rgb(203, 14, 78);
  }
  
  .details {
    display: flex;
  }
  
  .details img {
    width: 350px;
  }
  
  .popup-card {
    background-color: white;
    position: absolute;
    bottom: 0;
    width: 250px;
    padding: 20px 5px;
    display: flex;
    flex-direction: column;
  }
  
  .popup-header {
    font-size: 16px;
    font-weight: 500;
    color: rgb(112, 112, 112);
  }
  .popup-author {
    font-weight: 400;
    font-size: 12;
    color: rgb(178, 178, 178);
  }
  .popup-price {
    font-size: 16px;
    color: rgb(203, 14, 78);
  }
  .popup-card button {
    margin-top: 20px;
    margin-right: 30px;
    align-self: center;
  }
  
  .detailed-title {
    margin: 0;
    font-size: 25px;
    color: #707070;
  }
  .detailed-author {
    font-size: 17px;
    color: #2699fb;
  }
  .detailed-author span {
    color: #707070;
  }
  .detailed-text p {
    font-size: 14px;
    color: #707070;
  }
  .detailed-text span {
    color: #8a8989;
  }
  .detailed-price {
    font-size: 16px;
    color: rgb(203, 14, 78);
  }
  .detailed-details > button {
    margin-top: 10px;
  }
  .detailed-details .cart-quantity {
    margin-top: 10px;
  }
  .details-amount {
    color: #707070;
    font-size: 17px;
    font-weight: 700;
  }
  
  .detailed-product {
    padding: 10px 100px;
  }
  
  .details {
    display: flex;
    gap: 50px;
  }
  
  .synopsis {
    margin-left: 1vw;
    margin-top: 5vw;
    width: 80vw;
  }
  
  .synopsis p {
    color: rgb(112, 112, 112);
    font-size: 17px;
  }
  .synopsis h3 {
    padding-bottom: 10px;
    color: #707070;
    font-size: 21px;
    border-bottom: 2px solid;
  }
  